import { Injectable } from '@angular/core';

import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';

import { Observable } from '../../node_modules/rxjs';
import { AngularFireDatabase, AngularFireList  } from '../../node_modules/angularfire2/database';
import { Time } from '@angular/common';
import { timestamp } from 'rxjs/internal/operators/timestamp';

// Entity
import { User } from './entity/user/user';

// Helper classes
import { JsonConverter } from './entity/helper/json-converter';

// Session storage
import { SessionStorage, SessionStorageService } from 'angular-web-storage';
import { UserRoleService } from './user-role.service';

@Injectable({
  providedIn: 'root'
})
export class UserFirebaseService {

  dbPath: string = '/users/';

  usersObservable: Observable<any[]>;

  jsonConverter: JsonConverter = new JsonConverter();

  constructor(public afAuth: AngularFireAuth, private db: AngularFireDatabase, 
    public session: SessionStorageService, private urs: UserRoleService) { }

  // CRUD

  
  getUsers() {
    this.usersObservable = this.getList(this.dbPath);
  }


  getList(listPath): Observable<any[]> {
    return this.db.list(listPath).valueChanges();
  }

  /* 
  getUserByID(id: string) {
    let path = this.dbPath + id;
    this.db.object(path).valueChanges().subscribe(data => {
      let user = this.jsonToObj(JSON.stringify(data));
      this.setStorage(user);
      return user;
    });
  } */

  getUserByID(id: string) {
    return this.db.object(this.dbPath+id).valueChanges();
  }

  getUserByIndex(idx) {
    let path = this.dbPath+"/"+idx;
    this.db.object(path).valueChanges().subscribe(data => {
      this.setStorage(new User(data));
    });
  }

  //Test passed
  insertUser(user: User, id: string) {
    this.db.object(this.dbPath + id).update(user);
    this.urs.insertRole({fk_id: id, type: this.urs.userTypes.MEMBER});
   }
 
   updateUser(objwithUpdates, id: string) {
      this.db.object(this.dbPath+id).update(objwithUpdates);
   }
 
   deleteUser(key: string) {
     const usersRef = this.db.list(this.dbPath);
     usersRef.remove(this.convertEmailToKey(key));
     this.afAuth.auth.currentUser.delete();
   }

   // Session storage
   setStorage(user: User, expired: number = 0) {
    this.session.set("user", user, expired, 's');
   }

   getStorage(): User {
     let user =  this.session.get("user");
     return user;
   }

   private objToJSON(userObject : User): string {
     return JSON.parse(JSON.stringify(userObject));
   }

   private jsonToObj(json: string) : User {
     let obj : User = this.jsonConverter.convertJsonToUserObj(json);
     return obj;
   }

   convertEmailToKey(email: string) : string {
    return email.replace('.', '¤').replace('@', '%');
   }

   convertKeyToEmail(key: string) {
    return key.replace('¤','.').replace('%', '@');
   }

   clearSession() {
    this.session.remove("user");
   }

}
